// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

$bg-color: #fff;
$main-color: #000;

body {
    margin: 0px;
    padding: 0px;
    background-color: $bg-color;
}

.variant-input {
    margin-bottom:10px;
}

.variant-options {
    background-color: #efefef;
}

.text-bold {
    font-weight: bold;
}

.details {
    display: block;
    padding: 9.5px;
    margin: 0 0 10px;
    font-size: 13px;
    color: #333;
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.product-item {
    padding: 0px;
    padding-bottom: 100px;
    a {
        font-size: 1.1em;
    }
}

/* Customizations */

.tab-pane {
    padding-top:20px !important;
}

/* Helpers classes */

.text-left-not-xs, 
.text-left-not-sm, 
.text-left-not-md, 
.text-left-not-lg {
    text-align: left;
}

.text-center-not-xs, 
.text-center-not-sm, 
.text-center-not-md, 
.text-center-not-lg {
    text-align: center;
}
.text-right-not-xs, 
.text-right-not-sm, 
.text-right-not-md, 
.text-right-not-lg {
    text-align: right;
}
.text-justify-not-xs, 
.text-justify-not-sm, 
.text-justify-not-md, 
.text-justify-not-lg {
    text-align: justify;
}

@media (max-width: 767px) {
    .text-left-not-xs, 
    .text-center-not-xs, 
    .text-right-not-xs, 
    .text-justify-not-xs {
        text-align: inherit;
    }
    .text-left-xs {
        text-align: left;
    }
    .text-center-xs {
        text-align: center;
    }
    .text-right-xs {
        text-align: right;
    }
    .text-justify-xs {
        text-align: justify;
    }
}
@media (min-width: 768px) and (max-width: 991px) {
    .text-left-not-sm, 
    .text-center-not-sm, 
    .text-right-not-sm, 
    .text-justify-not-sm {
        text-align: inherit;
    }
    .text-left-sm {
        text-align: left;
    }
    .text-center-sm {
        text-align: center;
    }
    .text-right-sm {
        text-align: right;
    }
    .text-justify-sm {
        text-align: justify;
    }
}
@media (min-width: 992px) and (max-width: 1199px) {
    .text-left-not-md, 
    .text-center-not-md, 
    .text-right-not-md, 
    .text-justify-not-md {
        text-align: inherit;
    }
    .text-left-md {
        text-align: left;
    }
    .text-center-md {
        text-align: center;
    }
    .text-right-md {
        text-align: right;
    }
    .text-justify-md {
        text-align: justify;
    }
}
@media (min-width: 1200px) {
    .text-left-not-lg, 
    .text-center-not-lg, 
    .text-right-not-lg, 
    .text-justify-not-lg {
        text-align: inherit;
    }
    .text-left-lg {
        text-align: left;
    }
    .text-center-lg {
        text-align: center;
    }
    .text-right-lg {
        text-align: right;
    }
    .text-justify-lg {
        text-align: justify;
    }
}